.starter-btn {
  padding: 10px 20px;
  margin-right: 10px;
  border-radius: 5px;
  border-color: transparent;
  transition: all 0.3s;
  text-decoration: none;
  cursor: pointer;
}
.starter-btn span {
  display: inline-block;
  vertical-align: top;
  margin-right: 7px;
}

.starter-btn-blue {
  background: rgb(34, 122, 255);
  color: white;
}

.starter-btn-blue:hover,
.starter-btn-blue:active,
.starter-btn-blue:focus {
  background-color: rgba(34, 122, 255, 0.9);
  color: white;
}

.ms-lms-starter-info-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-column {
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  width: 720px;
  padding: 20px;
  margin: 0 0 20px;
  background-color: white;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.07);
  border-radius: 5px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-column:first-child {
  margin-top: 30px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-column:last-child {
  margin-top: 20px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links {
  max-width: 500px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links .starter-transients span {
  margin-top: 3px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links .starter-btn-blue span {
  margin-top: 2px;
  font-size: 14px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links-builders {
  position: absolute;
  top: -3px;
  right: 0;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-customizer .ms-lms-starter-info-box-customizer-title {
  color: #001931;
  font-size: 18px;
  font-weight: 700;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-customizer .ms-lms-starter-info-box-customizer-settings {
  display: flex;
  margin: 20px 0 0;
  padding: 0;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-customizer .ms-lms-starter-info-box-customizer-settings li {
  width: 50%;
  list-style: none;
  margin: 0;
  padding: 0 10px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-customizer .ms-lms-starter-info-box-customizer-settings li:first-child {
  padding-left: 0;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-customizer .ms-lms-starter-info-box-customizer-settings li:last-child {
  padding-right: 0;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-customizer .ms-lms-starter-info-box-customizer-settings li a {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  text-align: center;
  padding: 20px;
  transition: all 0.3s;
  background-color: #EEF1F7;
  text-decoration: none;
  color: #4D5E6F;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-customizer .ms-lms-starter-info-box-customizer-settings li a:hover {
  background-color: rgba(238, 241, 247, 0.62);
  color: #135e96;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-customizer .ms-lms-starter-info-box-customizer-settings li a span {
  margin: 0 7px 0 0;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links-plugin {
  display: flex;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links-plugin .ms-lms-starter-info-box-links-plugin-logo {
  margin: 0 0 15px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links-plugin .ms-lms-starter-info-box-links-plugin-info .ms-lms-starter-info-box-links-plugin-title {
  color: #273044;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links-plugin .ms-lms-starter-info-box-links-plugin-info .ms-lms-starter-info-box-links-plugin-title span {
  font-weight: 700;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links-plugin .ms-lms-starter-info-box-links-plugin-info .ms-lms-starter-info-box-links-plugin-list {
  margin: 15px 0 0;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links-plugin .ms-lms-starter-info-box-links-plugin-info .ms-lms-starter-info-box-links-plugin-list li {
  position: relative;
  padding: 0 0 0 20px;
  font-size: 12px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links-plugin .ms-lms-starter-info-box-links-plugin-info .ms-lms-starter-info-box-links-plugin-list li:before {
  content: "\e904";
  font-family: "ms-lms-icon";
  position: absolute;
  top: 3px;
  left: 0;
  text-align: center;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #227AFF;
  line-height: 14px;
  font-size: 7px;
  color: white;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links-plugin .ms-lms-starter-info-box-links-plugin-info .starter-btn {
  display: inline-block;
  vertical-align: top;
  margin-top: 15px;
  border-radius: 50px;
  text-transform: uppercase;
  font-size: 12px;
}
.ms-lms-starter-info-box .ms-lms-starter-info-box-links-plugin .ms-lms-starter-info-box-links-plugin-screen {
  margin: -20px -20px -20px auto;
}

@media (max-width: 1300px) and (min-width: 1200px) {
  .starter-column .logo img {
    width: 90%;
  }
  .content {
    margin-top: 30px;
    margin-left: 30px;
  }
  .starter-dashboard-text {
    margin-right: 30px;
  }
}
.has-content {
  position: relative;
}

.logo, .starter-dashboard-text {
  margin-bottom: 30px;
}

.actions {
  display: flex;
  align-items: center;
}

.documentation svg {
  width: 16px;
  height: 16px;
  fill: white;
  margin-right: 5px;
}

.documentation:hover {
  background: rgba(34, 122, 255, 0.75);
  color: white;
}

.changelog {
  background: #EEF1F7;
  color: #4D5E6F;
}

.changelog:hover {
  background: rgba(238, 241, 247, 0.62);
  cursor: pointer;
}

@media (max-width: 1024px) {
  .starter-row {
    flex-direction: column;
  }
}
.stm-loader-warning {
  position: relative;
  padding: 10px 15px 10px 30px;
  background-color: white;
  border-color: #227afb;
}

.stm-loader-warning p {
  color: #222222;
  text-decoration: none;
  font-size: 18px;
}

.stm-loader-warning .stm_lms_install_button {
  padding: 15px 30px;
  text-decoration: none;
  font-size: 18px;
  display: inline-block;
  text-align: center;
  font-weight: 700;
  background-color: #227afb;
  color: #fff !important;
  border-radius: 5px;
  transition: 0.3s ease;
  line-height: 1.3;
  border-color: #227afb;
}

.stm_lms_install_button:hover {
  background-color: #004ec4 !important;
  border-color: #004ec4 !important;
}

.updating {
  background-color: #004ec4 !important;
  border-color: #004ec4 !important;
}

.notice-wrapper {
  display: flex;
  justify-content: space-between;
}

.inner-wrapper {
  display: flex;
  align-items: center;
}

.inner-wrapper img {
  width: 40px;
  height: 40px;
  object-fit: cover;
  margin-right: 20px;
}

@media (max-width: 800px) {
  .notice-wrapper {
    flex-direction: column;
  }
}
.masterstudy-starter-banner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  width: 100%;
  min-height: 450px;
  max-width: 720px;
  margin: 0 auto 20px;
  padding: 30px 0 0;
  border-radius: 10px;
  background: #5339BD url("../images/banner.png") no-repeat 50% 100%;
  background-size: cover;
}
.masterstudy-starter-banner > div {
  max-width: 400px;
  text-align: center;
}
.masterstudy-starter-banner__title {
  margin-bottom: 10px;
  line-height: 34px;
  font-size: 30px;
  font-weight: 700;
  color: white;
}
.masterstudy-starter-banner__description {
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}
.masterstudy-starter-banner__buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 15px;
}
.masterstudy-starter-banner__buttons a {
  display: flex;
  padding: 14px 25px;
  margin: 0 5px;
  justify-content: center;
  align-items: center;
  border-radius: 58px;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(10px);
  text-decoration: none;
  font-size: 13px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.65px;
  text-transform: uppercase;
  color: white;
}
.masterstudy-starter-banner__buttons a.button-gradient {
  border: 1px rgba(255, 255, 255, 0.2);
  background: linear-gradient(180deg, white 0%, rgba(255, 255, 255, 0.7) 100%);
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.25);
  color: #227afb;
}
.masterstudy-starter-banner__templates {
  max-width: 100% !important;
}
.masterstudy-starter-banner__templates img {
  display: block;
  max-width: 100%;
  height: auto;
}